.navbar
    background-color: #fed924
    height: auto
    // width: 100%
    padding: 10px
    display: flex
    justify-content: space-between
    align-items: center
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif
    position: relative
    &_left
        padding-left: 15px

    
    &_left_logo
            width: 40px
            height: 40px
            margin-left: 0px
            margin-top: 0px
            cursor: pointer
            

            
            &_text
                font-size: 1.5rem
                font-weight: 600
                color: black
                letter-spacing: 1px
                text-transform: uppercase
                margin: 5px 0px
                h1
                    text-decoration: none
                
                
    &_center
        display: flex
        width: 85%
        height: auto
        
             
        &_list
            width: 550px
            display: flex
            flex-direction: row
            justify-content: space-between
            list-style: none
            font-weight: 200
            font-size: 20px
                
            &_li
                font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif
                color: black
                text-decoration: none
            &_li:hover
                color: white

    &_right
        display: flex
        width: 20%
        height: auto
        padding-right: 20px
        &_photo
            width: 55px
            height: 55px
            border-radius: 50%
        &_bienvenido
            display: flex
            align-items: center
            justify-content: space-around
            font-size: 1.5rem
            width: 300px
            color: black
            text-transform: uppercase
            margin: 5px 0px
        &_logout
           margin-bottom: 20px

        
             
        &_list
            width: 400px
            display: flex
            flex-direction: row
            justify-content: space-between
            list-style: none
            font-weight: 400
            font-size: 20px
           
           
            &_li
                font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif
                color: black
                text-decoration: none
                margin-left: 10px
            &_li:hover
                color: white

            
            
                
            


            
           
           
            